import React from "react";
import "./howToDoCard.scss";
import { SiReactos, SiCsswizardry } from "react-icons/si";
import PropTypes from "prop-types";

const HowToDoCard = ({ cardImg, cardTitle, cardIcon, id }) => {
  return (
    <div
      className="how-to-do-card"
      onClick={() => console.log(`${id}'li kart tıklandı`)}
    >
      <div
        className="how-to-do-card-image"
        style={{
          "--bg-img": `url(${cardImg})`,
        }}
      ></div>
      <div className="how-to-do-card-title">{cardTitle}</div>
      <div className="how-to-do-card-title-icon-container">
        <SiReactos
          style={{
            display:
              cardIcon === 1 ? "block" : cardIcon === 3 ? "block" : "none",
          }}
          size={20}
          className="how-to-do-card-title-icon"
        />
        <SiCsswizardry
          style={{
            display:
              cardIcon === 2 ? "block" : cardIcon === 3 ? "block" : "none",
          }}
          size={20}
          className="how-to-do-card-title-icon"
        />
      </div>
    </div>
  );
};
HowToDoCard.propTypes = {
  id: PropTypes.number.isRequired,
  cardImg: PropTypes.string.isRequired,
  cardTitle: PropTypes.string.isRequired,
  cardIcon: PropTypes.number.isRequired,
};
HowToDoCard.defaultProps = {
  id: 0,
  cardImg: "",
  cardTitle: "Özgür Küpeli",
  cardIcon: 0,
};
export default HowToDoCard;
