import React, { Component } from "react";
import "./header.scss";
import Hamburger from "../hamburger/Hamburger";
import Sidebar from "../sidebar/Sidebar";
import { NavLink } from "react-router-dom";
import { baseUrl } from "../../constants/constants";

export class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: 1,
      hamburger: false,
    };
  }
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
    window.location.href === `http://${baseUrl}/`
      ? this.setState({ selected: 1 })
      : this.setState({ selected: 2 });
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  setSelected = (value) => {
    this.setState({
      selected: value,
    });
  };
  setHamburger = () => {
    this.setState({
      hamburger: !this.state.hamburger,
    });
  };
  handleScroll = () => {
    const offset = window.scrollY;
    if (offset > 5) {
      this.setState({ hamburger: false });
    }
  };
  render() {
    return (
      <div className="navbar">
        <div className="logo" />
        <ul className="navbar-nav header-component-ic">
          <li
            className={`nav-item ${
              this.state.selected === 1 && "selectedItem"
            }`}
            onClick={() => this.setSelected(1)}
          >
            <NavLink className="nav-link" to="/">
              Who Am I
            </NavLink>
          </li>
          <li
            className={`nav-item ${
              this.state.selected === 2 && "selectedItem"
            }`}
            onClick={() => this.setSelected(2)}
          >
            <NavLink className="nav-link" to="/how-to-do">
              How To Do
            </NavLink>
          </li>
        </ul>
        <Sidebar
          selectedNav={this.state.selected}
          selectedFunc={this.setSelected}
          isOpen={this.state.hamburger}
        />
        <Hamburger
          hamburger={this.state.hamburger}
          hamburgerFunc={this.setHamburger}
        />
      </div>
    );
  }
}

export default Header;
