import React, { useState, useEffect } from "react";
import "./howToDo.scss";
//import useWindowWidthHeight from "../../hooks/useWindowWidthHeight";
import HowToDoHeader from "../howToDoHeader/HowToDoHeader";
import HowToDoCard from "../howTodoCard/HowToDoCard";
import { useHistory } from "react-router-dom";
import data from "../../services/data.json";

const HowToDo = () => {
  //const { width } = useWindowWidthHeight();
  let history = useHistory();
  const isReactOrNative = () => {
    if (history.location.pathname === `/how-to-do/css`) {
      return 1;
    }
    if (history.location.pathname === `/how-to-do/react`) {
      return 2;
    }
    if (history.location.pathname === `/how-to-do/style`) {
      return 3;
    }
    if (history.location.pathname === `/how-to-do/native`) {
      return 4;
    }
    return 0;
  };
  const [dropdownSelected, setDropdownSelected] = useState(isReactOrNative);

  useEffect(() => {
    if (history.location.pathname === `/how-to-do`) {
      setDropdownSelected(0);
    }
    if (history.location.pathname === `/how-to-do/css`) {
      setDropdownSelected(1);
    }
    if (history.location.pathname === `/how-to-do/react`) {
      setDropdownSelected(2);
    }
    if (history.location.pathname === `/how-to-do/style`) {
      setDropdownSelected(3);
    }
    if (history.location.pathname === `/how-to-do/native`) {
      setDropdownSelected(4);
    }
  }, [history.location.pathname]);

  const contentIconHesapla = (string) => {
    // 1-react 2-css 3react,css
    if (string.length > 5) {
      return 3;
    }
    if (string.length === 5) {
      return 1;
    }
    return 2;
  };

  const dataListFilterFunc = (data) => {
    if (history.location.pathname === `/how-to-do`) {
      return data;
    }
    if (history.location.pathname === `/how-to-do/css`) {
      let arr = data.filter((i) => {
        return i.category === "css";
      });
      return arr;
    }
    if (history.location.pathname === `/how-to-do/react`) {
      let arr = data.filter((i) => {
        return i.category === "react";
      });
      return arr;
    }
    if (history.location.pathname === `/how-to-do/style`) {
      let arr = data.filter((i) => {
        return i.category === "style";
      });
      return arr;
    }
    if (history.location.pathname === `/how-to-do/native`) {
      let arr = data.filter((i) => {
        return i.category === "native";
      });
      return arr;
    }
  };

  return (
    <div className="content-box">
      <HowToDoHeader
        dropdownSelected={dropdownSelected}
        setDropdownSelected={setDropdownSelected}
      />
      <div className="content-content-box">
        {dataListFilterFunc(data).map((i) => (
          <HowToDoCard
            key={i.id}
            id={i.id}
            cardImg={i.cardImg}
            cardTitle={i.cardTitle}
            cardIcon={contentIconHesapla(i.cardIcon)}
          />
        ))}
      </div>
    </div>
  );
};

export default HowToDo;
